@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal; }

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal; }

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-semibold.woff2") format("woff2"), url("../fonts/montserrat-semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal; }

html {
  box-sizing: border-box;
  -moz-osx-font-smoothing: grayscale; }

*,
*::before,
*::after {
  box-sizing: inherit; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

body {
  font-family: "Montserrat", "Arial", sans-serif;
  position: relative;
  padding: 0;
  height: 100%; }

.button {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 14px;
  line-height: 17px;
  display: block;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  border-radius: 50px;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 14px;
  padding-bottom: 14px;
  min-height: 42px; }

.button:hover {
  opacity: 0.5; }

.button--record {
  background-color: #ffffff;
  color: #2f80ed;
  text-transform: none;
  width: 160px; }

.button--promo {
  background-color: #64e2cd;
  color: #ffffff;
  font-size: 14px;
  font-weight: 700;
  padding-top: 18px;
  width: 192px;
  min-height: 51px;
  box-shadow: 0 0 0 11px rgba(100, 226, 205, 0.5); }

@media (max-width: 1439px) {
  .button--record {
    background-color: transparent;
    color: #ffffff;
    width: 160px; } }

.top-wrapper {
  position: relative;
  background: linear-gradient(90deg, #ffffff 50%, transparent 0), linear-gradient(#2f80ed 662px, #eaf2fd 662px, #eaf2fd 717px, transparent 0);
  background-size: 100% 100%; }

.top-wrapper__element {
  margin-left: auto;
  margin-right: auto;
  position: relative;
  padding-top: 31px;
  padding-bottom: 50px;
  max-width: 1440px;
  background-color: #ffffff;
  background-image: url("../img/top-background.png");
  background-repeat: no-repeat;
  background-position: left top; }

@media (max-width: 1439px) {
  .top-wrapper {
    background: url("../img/top-background.png");
    background-repeat: no-repeat;
    background-position: -18px top; }
  .top-wrapper__element {
    background: none;
    padding-top: 27px;
    width: 95.83%;
    min-width: 768px; } }

@media (max-width: 767px) {
  .top-wrapper__element {
    padding-top: 29px; } }

.header {
  margin-left: auto;
  margin-right: auto;
  display: flex;
  position: relative;
  max-width: 1175px; }

.header__cities-list {
  width: 27.4%;
  margin-right: 158px; }

.header__menu {
  display: flex;
  align-items: center;
  width: 40.17%; }

.header__button {
  margin-left: auto; }

@media (max-width: 1439px) {
  .header__cities-list {
    width: 21.6%;
    margin-right: 0; }
  .header__menu {
    display: none; }
  .header__button {
    margin-left: 11.35%; } }

.cities-list {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
  font-size: 14px;
  line-height: 17px;
  display: flex;
  align-items: center; }

.cities-list__item a {
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
  margin-left: 25px; }

.cities-list__item a:hover {
  opacity: 0.5; }

.cities-list__item:first-child a {
  margin-left: 0;
  font-weight: 700;
  color: #2f80ed; }

@media (max-width: 1439px) {
  .cities-list {
    margin-left: 16px; }
  .cities-list__item {
    display: none; }
  .cities-list__item:first-child {
    display: block; }
  .cities-list__item:first-child a {
    position: relative; }
  .cities-list__item:first-child a::before {
    position: absolute;
    content: "";
    top: 8px;
    left: calc(100% + 15px);
    width: 0;
    height: 0;
    border: 3px solid transparent;
    border-top: 3px solid #2f80ed; } }

.header-menu-list {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
  font-size: 14px;
  line-height: 17px;
  display: flex;
  justify-content: space-between;
  width: 100%; }

.header-menu-list__item a {
  text-decoration: none;
  color: #ffffff;
  width: 100%; }

.header-menu-list__item:last-child a {
  margin-right: 0; }

.header-menu-list__item a:hover {
  text-shadow: 0 0 1.5px #ffffff, 0 0 1.5px #ffffff; }

.main-section {
  margin-left: auto;
  margin-right: auto;
  display: flex;
  position: relative;
  max-width: 1100px; }

.main-section__review {
  margin-top: 367px;
  margin-left: 15px; }

.main_section__promo {
  margin-top: 86px;
  margin-left: 324px;
  width: 552px; }

@media (max-width: 1439px) {
  .main-section__review {
    margin-top: 400px;
    margin-left: 45px; }
  .main_section__promo {
    margin-top: 91px;
    margin-left: 2.47%;
    width: 61.72%; } }

.review {
  color: #2f80ed;
  min-height: 231px;
  width: 231px;
  border-radius: 50%;
  background: radial-gradient(68.31% 68.31% at 50% 50%, #f9fbff 0%, #cadef9 100%), #ffffff;
  box-shadow: 0 10px 50px #d6e2ff; }

.review h2 {
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  margin-top: 47px;
  margin-bottom: 0;
  margin-left: 83px; }

.review__grade {
  font-weight: 700;
  font-size: 34px;
  line-height: 52px;
  margin-top: 0;
  margin-bottom: 0;
  margin-left: 51px; }

.review__name {
  font-size: 14px;
  line-height: 20px;
  margin-top: 0;
  margin-bottom: 0;
  margin-left: 53px; }

.review__nick {
  font-weight: 700;
  font-size: 14px;
  line-height: 20px;
  margin-top: 0;
  margin-bottom: 9px;
  margin-left: 80px; }

.review__anchor {
  text-decoration: none;
  color: #2f80ed;
  font-size: 10px;
  line-height: 20px;
  margin-left: 74px; }

.review__anchor:hover {
  opacity: 0.5; }

.promo {
  color: #ffffff; }

.promo h2 {
  font-weight: 600;
  font-size: 16px;
  line-height: 20px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  margin-top: 0;
  margin-bottom: 20px; }

.promo__bold-text {
  font-weight: 700;
  font-size: 40px;
  line-height: 50px;
  margin-top: 0;
  margin-bottom: 20px;
  width: 515px; }

.promo__text {
  font-size: 18px;
  line-height: 22px;
  margin-top: 0;
  margin-bottom: 40px; }

.promo__button {
  margin-left: 15px; }

@media (max-width: 1439px) {
  .promo h2 {
    font-size: 14px;
    line-height: 17px;
    margin-bottom: 19px; }
  .promo__bold-text {
    font-size: 36px;
    line-height: 45px;
    margin-bottom: 32px;
    width: 96%; }
  .promo__text {
    margin-bottom: 46px; } }
